import React from 'react';
import { Grid, Typography, Link } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import FacebookIcon from '@material-ui/icons/Facebook';
import IconButton from '@material-ui/core/IconButton';
import TwitterIcon from '@material-ui/icons/Twitter';
import InstagramIcon from '@material-ui/icons/Instagram';
import cup from './cup.png';
import { Divider } from '@material-ui/core';


const useStyles = makeStyles((theme) => ({
    gridStyle: {
        display: 'flex',
        marginTop: theme.spacing(10),
        marginBottom: theme.spacing(12),
        alignItems: 'flex-end',
        justifyContent : 'center',
        color: 'rgb(250, 250, 250)'
    },
    paraStyle: {
        display: 'flex',
        marginTop: theme.spacing(1),
        marginBottom: theme.spacing(1),
        alignItems: 'center',
        justifyContent : 'center'
    }


}));

export default function Slider() {
    const classes = useStyles();
    
    return(
    <div id="home" style={{backgroundColor:"#212121"}}>
        <Grid container sm={12}>
            <Grid container sm={6}>
                <Grid item>
                    <Typography style={{fontSize: '20px', marginLeft: '100px', marginTop: '100px', color: 'rgb(100, 100, 100'}}>{'~./home__________'}</Typography>
                </Grid>
                <Grid container sm={12} direction="row" className={classes.gridStyle}>
                
                    <Grid item >
                        <Typography variant='h4'>
                            <span style={{color:'rgb(150,150,150)'}}>i'm</span><span style={{color:'#212121'}}>__</span>
                        </Typography>
                    </Grid>
                    <Grid item>
                        <Typography variant='h3'>
                            musfiqRifat
                        </Typography>
                        
                    </Grid>
                    <Grid item >
                        <Typography variant='h5'>
                            <span style={{color:'#212121'}}>___</span><span style={{color:'rgb(150,150,150)'}}>bangladesh</span>
                        </Typography>
                    </Grid>
                </Grid>
                <Grid container sm={12} direction="row" className={classes.paraStyle} >
                    <Grid item >
                        <Typography  variant='h5' style={{color:'#ffff8d'}}>
                            25 y.o.<span style={{color:'#212121'}}>__</span>engineering()
                        </Typography>
                        
                    </Grid>
                </Grid>
                <Grid container sm={12} direction="row" className={classes.paraStyle} >
                    <Grid item >
                        <Typography variant='h6' style={{color:'#ccff90'}}>
                            {'<App Developer />'}<span style={{color:'#212121'}}>___</span>
                        </Typography>
                    </Grid>
                    <Grid item >
                        <Typography variant='h6' style={{color:'#ffe57f'}}>
                            {'<App Designer />'}
                        </Typography>
                    </Grid>
                </Grid>

                <Grid container sm={12} className={classes.paraStyle}>
                    
                    <Grid item>
                    <a href="https://www.facebook.com/musfiq.rifat7" target='_blank'>
                    <IconButton aria-label="facebook" style={{color:'#84ffff'}}>
                        <FacebookIcon fontSize='large'/>
                    </IconButton>
                    </a>
                    </Grid>

                    <Grid item>
                    <a href="https://twitter.com/Musfiq420" target='_blank'>
                    <IconButton aria-label="twitter" style={{color:'#84ffff'}}>
                        <TwitterIcon fontSize='large'/>
                    </IconButton>
                    </a>
                    </Grid>
                    
                    <Grid item>
                    <a href="https://www.instagram.com/musfiqrifat" target='_blank'>
                    <IconButton aria-label="instagram" style={{color:'#84ffff'}}>
                        <InstagramIcon fontSize='large'/>
                    </IconButton>
                    </a>
                    </Grid>
                </Grid>

            </Grid>
        
            <Grid container sm={6} className={classes.paraStyle}>
                <Grid item>
                    <h1><br/></h1>
                    <img paddingBottom='0px' src={cup} width='300px' height='250px'/>
                    <Typography variant='h5'style={{color:'#fbf3a9'}}>
                        <br/>love to have a cup of code<br/>with 2 spoons of color
                    </Typography>
                </Grid>
                <Grid item >
                    
                </Grid>
            </Grid>
        </Grid>
            <Divider variant="fullWidth" style={{marginTop: '50px'}}/>
    </div>
    )
}