import React from 'react';
import { AppBar, Toolbar, Typography, Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import HomeIcon from '@material-ui/icons/Home';
import { Avatar } from '@material-ui/core';
import { createMuiTheme } from '@material-ui/core/styles';
import { deepOrange } from '@material-ui/core/colors';
import { Link } from 'react-router-dom';

import { Link as Scroll } from "react-scroll";


const useStyles = makeStyles((theme) => ({
  
  Typographystyle:{
    display: "flex",
    flexGrow: 1,
    justifyContent: 'flex-start',
    marginLeft: theme.spacing(2)
  },
  orange: {
    color: theme.palette.getContrastText(deepOrange[500]),
    backgroundColor: deepOrange[500],
  },
  navBarStyle: {
    color: '#e8ddd8',
    textTransform: "lowercase",
    margin: "0px",
    padding: '0px'
  }
  
}));


const Header = () => {
    const classes = useStyles();
    const theme = createMuiTheme();
return (
<AppBar position="sticky" color='secondary'>
  <Toolbar >
    <Avatar className={classes.orange}>M</Avatar>
    <Typography className={classes.Typographystyle}>
      m u s f i q ' s__p o r t f o l i o
    </Typography>
    <div display="flex" flexDirection="row">
      
    <Scroll
        to="home"
        duration={400}
        smooth={true}
      >
      <Button className={classes.navBarStyle}> h o m e</Button>
      </Scroll>
      <Button disabled style={{color: "rgb(100,100,100)"}} className={classes.navBarStyle} disabled>________</Button>
      
      <Scroll
        to="services"
        duration={400}
        smooth={true}
      >
      <Button className={classes.navBarStyle}>S e r v i c e s</Button>
      </Scroll>
      <Button disabled style={{color: "rgb(100,100,100)"}} className={classes.navBarStyle} disabled>________</Button>
      
      <Scroll
        to="portfolio"
        duration={400}
        smooth={true}
      >
      <Button className={classes.navBarStyle}>P o r t f o l i o</Button>
      </Scroll>
      <Button disabled style={{color: "rgb(100,100,100)"}} className={classes.navBarStyle} disabled>________</Button>
      
      <Scroll
        to="contact"
        duration={400}
        smooth={true}
      >
      <Button className={classes.navBarStyle}>C o n t a c t</Button>
      </Scroll>

    
    </div>
    
    
  </Toolbar>
</AppBar>
    );
};

export default Header;